/**
*	application main stylesheet
*/

*{
	box-sizing: border-box;
}

// base variables
	@import 'vars/_all';
	
// mixins
	@import 'mixins/_all';
	
// blood division partials
	@import 'app/_partials/_all';

// views
	@import 'app/views/_all';
	
// landing page stylesheet
	@import 'welcome';
	
#app-layout{
	padding-bottom:0px;
	background: #E9EAED; 
	overflow-x:hidden;
	.overlay{
		position:fixed;
		top: 0px;
		bottom:0px;
		left: 0px;
		right:0px;
		background: #000;
		opacity: 0.8;
		display:none;
		z-index:9999;
	}
	.btn{
		-webkit-border-radius: 0px 0px 0px 0px; 
		border-radius: 0px 0px 0px 0px;
		background: #f39c12;
		text-decoration: uppercase;
		border:none;
		color: #FFF; 
		&:hover{
			background: #e74c3c;
			color: #FFF; 
			-webkit-transition:ease-in-out 0.2s all;
			 -moz-transition:ease-in-out 0.2s all;
			 -ms-transition:ease-in-out 0.2s all;
			 -o-transition:ease-in-out 0.2s all;
			 transition:ease-in-out 0.2s all; 
		}

		&:not(:hover){
			-webkit-transition:ease-in-out 0.2s all;
			 -moz-transition:ease-in-out 0.2s all;
			 -ms-transition:ease-in-out 0.2s all;
			 -o-transition:ease-in-out 0.2s all;
			 transition:ease-in-out 0.2s all; 
		}
	}

	.form-control{
		-webkit-border-radius: 0px 0px 0px 0px; 
		border-radius: 0px 0px 0px 0px;
		border:none;
		background: #BDC3C7; 
		
		&:placeholder-shown{
			color: #fff; 
		}
	}
}

#profile{
	background: #E9EAED; 
	
}

.overlay{
		position:fixed;
		top: 0px;
		bottom:0px;
		left: 0px;
		right:0px;
		background: #000;
		opacity: 0.8;
		display:none;
		z-index:9999;
		height: 100%;
		width: 100%;  
	}

.none{
	display:none;
}

#search{
	.container-fluid.search-wrapper{
		width: 100%;
		height: 100%;
		padding: 0px; 
		background: #e5e5e5; 
		
		.overlay{
			position:fixed;
			top: 0px;
			left: 0px;
			right: 0px;
			bottom:0px;
			background: #000;
			opacity: 0.8;
		  	display:none;
		  	height: 100%;
		  	width: 100%;  
		}
		p{
			font-size:13px;
			padding-left:15px;
		}
		.header{
			padding-top:10px;
			margin-bottom:10px;
			padding-bottom:3px;
			background: #fff;
			width: 100%; 
			position:fixed;
			z-index:9999;
			height:52px;
			.title{
				margin-top:-3px;
			}
			.form-control{
				background: #FFF;
				border:1px solid #ccc; 
			}

			.results{
			    width: 91.5%;
			    height: 300px;
			    background: #FFF;
			    position: absolute;
			    top: 34px;
			    z-index: 9999;
			    margin-left: 40px;
			    overflow-y:scroll;
			    border-left:1px solid #e5e5e5;
			    border-right: 1px solid #e5e5e5;
			    border-bottom: 1px solid #e5e5e5;
    			.result{
					padding:10px 40px 10px 40px;
					&:hover{
						background: #e5e5e5; 
					}


				}   
			}

			.categories{
				padding-top:6px;
				padding-left:0px;
				span{
					margin-left:8px;
					font-size:11px;
				}
			}
			.blue{
				height:5px;
				width: 25%;
				position:absolute;
				top:52px;
				left: 0px; 
				background: #3498DB;  
			}

			.green{
				height:5px;
				width: 25%;
				position:absolute;
				top:52px;
				left: 25%; 
				background: #2ecc71;  
			}

			.red{
				height:5px;
				width: 25%;
				position:absolute;
				top:52px;
				left: 50%; 
				background: #e74c3c; 
			}

			.orange{
				height:5px;
				width: 25%;
				position:absolute;
				top:52px;
				left: 75%; 
				background: #f39c12; 
			}
		} 

		.content{
			width:63%;
			padding-top:70px;
			border-left:1px solid #e5e5e5;
			border-right:1px solid #e5e5e5;
			background: #fff; 
			.result{
				padding: 10px;
				margin: 10px 0px 10px 0px;
				&:hover{
					background: #ddd;
				}

				.media{
					a{
						img{
							height: 150px;
							width: 150px;
						}
					}
					.media-body{
						h4{
							margin-left:14px;
						}
					}
				}
			}

			.affix{
				left: 63.5%;
    			width: 17%; 
			}

			.category{
				border-bottom:1px solid #e5e5e5;
			}
		} 
	}
}